@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --text-legend: Roboto;
  --caption-4-medium: Inter;

  /* font sizes */
  --font-size-8xs-8: 4.8px;
  --text-legend-size: 12px;
  --font-size-6xs-4: 6.4px;
  --font-size-5xs-2: 7.2px;
  --font-size-base-8: 15.8px;
  --font-size-3xs-6: 9.6px;
  --font-size-2xs-8: 10.8px;
  --font-size-4xl-7: 23.7px;
  --font-size-base: 16px;
  --caption-2-medium-size: 18px;
  --font-size-21xl: 40px;
  --caption-4-medium-size: 13px;
  --font-size-xl: 20px;
  --text-h1-size: 50px;

  /* Colors */
  --blue: #0088f0;
  --red: #ef2a82;
  --text-secondary: #8794ba;
  --chart-lines: #eeeef6;
  --color-white: #fff;
  --text-main: #172755;
  --color-steelblue: #465b95;
  --color-midnightblue: #060d44;
  --color-gray-100: #fdfdfd;
  --color-gray-200: #0d0d0d;
  --color-gray-300: rgba(255, 255, 255, 0.3);
  --color-gray-400: rgba(255, 255, 255, 0.5);
  --color-gray-500: rgba(255, 255, 255, 0.1);
  --color-lightcoral: #fa637e;
  --color-snow: #fff9f9;
  --color-royalblue-100: #6c7feb;
  --color-royalblue-200: #5e73e8;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-smi-8: 12.8px;
  --gap-12xs-4: 0.4px;
  --gap-12xs-6: 0.6px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-34xl: 53px;
  --padding-11xs: 2px;
  --padding-61xl: 80px;

  /* border radiuses */
  --br-xs: 12px;
  --br-xs-2: 11.2px;
  --br-11xl: 30px;
  --br-13xl: 32px;
  --br-49xl: 68px;
  --br-3xs: 10px;
}
