.signUp {
  position: relative;
  letter-spacing: -0.3px;
  line-height: 20px;
  font-weight: 500;
}
.textStatesMediumMedium {
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 33.5px);
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-11xs);
  align-items: center;
  justify-content: flex-start;
}
.buttonsFixedLayout50px {
  position: absolute;
  height: calc(100% - 480px);
  width: 100%;
  top: 480px;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-royalblue-100);
  overflow: hidden;
  font-size: var(--caption-2-medium-size);
  color: var(--color-gray-200);
  font-family: var(--caption-4-medium);
}
.email {
  position: relative;
  letter-spacing: -0.3px;
  line-height: 20px;
}
.textStatesRegularMedium {
  position: absolute;
  top: calc(50% - 10px);
  left: 16px;
  display: none;
  flex-direction: row;
  padding: 0 var(--padding-11xs);
  align-items: center;
  justify-content: flex-start;
  font-size: var(--caption-2-medium-size);
}
.error,
.password {
  position: absolute;
  letter-spacing: -0.5px;
  line-height: 18px;
}
.error {
  top: -28px;
  right: 0;
  color: var(--color-lightcoral);
  text-align: right;
  display: none;
}
.password {
  top: -25px;
  left: 0;
  color: var(--color-gray-400);
}
.inputs50pxText,
.inputs50pxText1 {
  position: absolute;
  top: 294px;
  left: 1px;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 1px solid var(--color-gray-500);
  box-sizing: border-box;
  width: 410px;
  height: 50px;
}
.inputs50pxText1 {
  top: 194px;
}
.signUp1 {
  position: absolute;
  top: 0;
  left: 0;
  font-size: var(--font-size-21xl);
  letter-spacing: -1.2px;
  line-height: 44px;
  font-weight: 500;
  font-family: var(--caption-4-medium);
  display: flex;
  align-items: center;
  width: 390px;
}
.inputs50pxText2 {
  position: absolute;
  top: 102px;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 1px solid var(--color-gray-500);
  box-sizing: border-box;
  width: 190px;
  height: 50px;
}
.signIn {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.inputs50pxText3,
.inputs50pxText4 {
