.dateChild {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-white);
}
.dateChild,
.dateItem,
.div {
  position: absolute;
}
.dateItem {
  top: 6px;
  left: 13px;
  border-radius: 50%;
  background-color: var(--red);
  width: 39px;
  height: 39px;
  display: none;
}
.div {
  top: calc(50% - 9px);
  left: calc(50% - 4.29px);
  line-height: 140%;
}
.date {
  position: relative;
  width: 66px;
  height: 52px;
  text-align: center;
  font-size: var(--text-legend-size);
  color: var(--text-main);
  font-family: var(--text-legend);
}
