.signUp,
.typemenu {
  position: relative;
}
.typemenu {
  border-radius: var(--br-49xl);
  border: 1px solid var(--color-steelblue);
  box-sizing: border-box;
  height: 40px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-34xl);
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--text-secondary);
  font-family: var(--text-legend);
}
