.looperBgIcon {
  position: absolute;
  top: 0;
  left: 0;
  width: 1280px;
  height: 720px;
  opacity: 0.2;
}
.aboutUs,
.stockPricePrediction {
  position: absolute;
  letter-spacing: -1.2px;
  line-height: 44px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 683px;
}
.aboutUs {
  top: 0;
  left: 0;
  height: 22px;
}
.stockPricePrediction {
  top: 169px;
  left: calc(50% - 332px);
  font-size: var(--font-size-xl);
  height: 200px;
}
.signIn {
  position: absolute;
  top: 98px;
  left: 264px;
  width: 702px;
  height: 369px;
}
.signUp {
  position: relative;
  background-color: var(--color-midnightblue);
  width: 100%;
  height: 720px;
  overflow: hidden;
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
  font-family: var(--caption-4-medium);
}
